.wg-block-days {
	.month {
		border-top: 1px solid $wg-gray-border;
		background-color: $wg-light-gray;
		height: 44px;
		line-height: 44px;
		text-align: center;
		color: $wg-gray-text;
		font-weight: 500;
		font-size: medium;
	}
	.week {
		font-weight: 500;
		border-top: 1px solid $wg-gray-border;
		background-color: $wg-cream;
	}
	.item-divider {
		text-align: center;
		border-top: 1px solid #ddd;
	}
	.total-time-off {
		padding: 10px;
		background: $light;
		border-bottom: 1px solid $wg-gray-border;
		h1, p {
			color: $wg-gray-text;
		}
	}

	.ion-cal {
		background-color: $light;
	    i.left-icon,
	    i.right-icon {
	      color: $positive;
	    }
	    .days .day {
	        border: 2px solid $light;
            border-radius: 50%;
            transition: all 0.1s;
            &.blocked.event:before {
        		content: none;
        		height: 0;
        		width: 0;
            }
	        &.blocked:not(.selected):not(.disabled) {
	            border-radius: 4px; // inner radius is -2px of current value
	            background-color: #F86969;
	            color: $light;
	        }
	    }
	}
}